* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
  background: linear-gradient(90deg,
      #6da8f5 0%,
      #A0C9FF 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
}

.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 445px;
  min-height: 300px;
  background: #e7f2ff;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
  box-shadow: -10px -10px 10px rgba(255, 255, 255, 0.2),
    10px 10px 10px rgba(0, 0, 0, 0.1);
}

h1 {
  margin: 32px 0;
  font-size: 33px;
}

.complete {
  text-decoration: line-through;
  background: linear-gradient(90deg,
      #A0C9FF 0%,
      #4a99ff 100%) !important;
  color: #232E35 !important;
}

.todo-form {
  margin-bottom: 16px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid rgba(17, 122, 255, .7);
  outline: none;
  width: 320px;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(90deg,
      rgba(17, 122, 255, 1) 0%,
      rgba(20, 159, 255, 1) 100%);
  color: white;
  text-transform: capitalize;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: white;
  background: linear-gradient(90deg,
      rgba(20, 159, 255, 1) 0%,
      rgba(17, 122, 255, 1) 100%);
  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.iconsContainer {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.seperator {
  height: 1px;
  width: 75%;
  align-self: center;
  background-color: #232E35;
  margin-top: 5px;
  margin-bottom: 16px;
}